    methods: {
        close: function () {
            let dialog = this.dialog;
            let me = this;
            if(!dialog){
                return me.$bvModal.hide()
            }
        },
        selectItem(adAccion) {
            let me = this
            if (adAccion === 1) {
                me.product.plans = me.plans.basic.title;
                me.product.price = me.plans.basic.price;
                me.product.description = me.plans.basic.description;
                me.dialog = true;
                me.modalShow = true;
            } else if (adAccion === 2) {
                me.product.plans = me.plans.business.title;
                me.product.price = me.plans.business.price;
                me.product.description = me.plans.business.description;
                me.dialog = true;
                me.modalShow = true;
            } else {
                me.product.plans = me.plans.professional.title;
                me.product.price = me.plans.professional.price;
                me.product.description = me.plans.professional.description;
                me.dialog = true;
                me.modalShow = true;
            }
        }
    },
    created() {
        this.close();
    }
}
