<template>
  <div>
    <div class="page-header page-header-small">
      <parallax
        class="page-header-image"
        style="background-image: url('img/negocio.jpg')"
      >
      </parallax>
      <div class="content-center">
        <div class="container">
          <h1 class="title">Contacto.</h1>
          <div class="text-center">
            <a href="https://web.facebook.com/TedescoDev/" target="_blank" class="btn btn-primary btn-icon btn-round">
            <i class="fab fa-facebook-square"></i>
            </a>
            <a href="https://twitter.com/TedescoDev" target="_blank" class="btn btn-primary btn-icon btn-round">
            <i class="fab fa-twitter"></i>
            </a>
            <a href="https://www.instagram.com/tedescodev/" target="_blank" class="btn btn-primary btn-icon btn-round">
            <i class="fab fa-instagram"></i>
            </a>
          </div>
        </div>
      </div>
    </div>
   
    <div class="section section-contact-us text-center">
        <div class="container">
          <h2 class="title">Quieres trabajar con nosotros?</h2>
          <p class="description">Construyamos juntos.</p>
          <form 
            ref="form"
          >
            <div class="row">
              <div class="col-lg-6 text-center ml-auto mr-auto col-md-8">
                <fg-input
                  class="input-lg"
                  placeholder="Nombre y Apellido..."
                  :rules="nameRules"
                  required
                  v-model="form.firstName"
                  :counter="10"
                  addon-left-icon="now-ui-icons users_circle-08"
                >
                </fg-input>
                <fg-input
                  class="input-lg"
                  placeholder="Asunto..."
                  :rules="subjectRules"
                  required
                  :counter="10"
                  v-model="form.subject"
                  addon-left-icon="now-ui-icons users_circle-08"
                >
                </fg-input>
                <fg-input
                  class="input-lg"
                  placeholder="Email..."
                  required
                  :rules="emailRules"
                  v-model="form.email"
                  addon-left-icon="now-ui-icons ui-1_email-85"
                >
                </fg-input>
                <div class="textarea-container">
                  <textarea
                    class="form-control"
                    name="name"
                    rows="4"
                    cols="80"
                    :rules="messageRules"
                    required
                    v-model="form.message"
                    placeholder="Escribe un mensaje..."
                  ></textarea>
                </div>
                <div class="send-button">
                  <n-button 
                    type="primary"
                    round 
                    block 
                    size="lg"
                    @click="sendEmail">
                    Enviar mensaje
                  </n-button>
                </div>
              </div>
            </div>
          </form>
        </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { Button, FormGroupInput } from '@/components';
import swal from 'sweetalert';

export default {
  name: 'contact',
  bodyClass: 'landing-page',
  components: {
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      valid: false,
      form: {
        firstName: '',
        email: '',
        subject: '',
        message: '',
      },
      nameRules: [
          v => !!v || 'Nombre y apellido requerido',
          v => v.length <= 15 || 'El nombre o apellido debe tener menos de 15 caracteres',
      ],
      subjectRules: [
        v => !!v || 'El asunto es requerido',
        v => v.length <= 10 || 'El asunto debe tener menos de 10 caracteres',
      ],
      emailRules: [
        v => !!v || 'Un E-mail para contactarte es requerido',
        v => /.+@.+/.test(v) || 'El E-mail no es valido',
      ],
      messageRules: [
        v => !!v || 'Debe escribirnos un mensaje',
        v => v.length <= 100 || 'Escriba al menos 100 caracteres',
      ],
    };
  },
  methods: {
    limpiar() {
      this.form.firstName = "";
      this.form.email = "";
      this.form.message = "";
      this.form.subject = "";
    },

    sendEmail: async function() {
      debugger;
      let me = this;
      let data 	= {
        name: this.form.firstName,
        email: this.form.email,
        subject: this.form.subject,
        message: this.form.message
      }
      await axios.post(
        "/email/contact",data
      )
      .then(() => {
        debugger;
        swal({
          title: "Buen trabajo!",
          text: "Uno de nuestros colaboradores se comunicara con usted a la brevedad.",
          icon: "success"
        });
        me.limpiar();
      }).catch( () => {
        swal({
          title: "Lo siento",
          text: "Ocurrio un problema con el servidor",
          icon: "error"
        });
      })
    }
  }
};
</script>
