a, ofrecido mediante una decisin afirmativa y positiva, antes del tratamiento inicial, removible y documentado.\n            Cookies propias\n            Son aquellas cookies que son enviadas al ordenador o dispositivo del Usuario y gestionadas exclusivamente por TEDESCO DEV para el mejor funcionamiento del Sitio Web. La informacin que se recaba se emplea para mejorar la calidad del Sitio Web y su Contenido y su experiencia como Usuario. Estas cookies permiten reconocer al Usuario como visitante recurrente del Sitio Web y adaptar el contenido para ofrecerle contenidos que se ajusten a sus preferencias.\n            La(s) entidad(es) encargada(s) del suministro de cookies podr(n) ceder esta informacin a terceros, siempre y cuando lo exija la ley o sea un tercero el que procese esta informacin para dichas entidades. \n            Cookies de redes sociales\n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            TEDESCO DEV incorpora plugins de redes sociales, que permiten acceder a las mismas a partir del \n            Sitio Web. Por esta razn, las cookies de redes sociales pueden almacenarse en el navegador del \n            Usuario. Los titulares de dichas redes sociales disponen de sus propias polticas de proteccin \n            de datos y de cookies, siendo ellos mismos, en cada caso, responsables de sus propios ficheros \n            y de sus propias prcticas de privacidad. El Usuario debe referirse a las mismas para informarse \n            acerca de dichas cookies y, en su caso, del tratamiento de sus datos personales. nicamente a \n            ttulo informativo se indican a continuacin los enlaces en los que se pueden consultar dichas \n            polticas de privacidad y/o de cookies:"),_c('br'),_c('a',{attrs:{"href":"https://www.facebook.com/policies/cookies/","target":"_blank","rel":"noopener"}},[_vm._v("Facebook, ")]),_c('a',{attrs:{"href":"https://twitter.com/es/privacy/","target":"_blank","rel":"noopener"}},[_vm._v("Twitter, ")]),_c('a