<template>
  <div class="container">
    <div>
      <div v-if="!paidFor">
        <h4>Estas a punto de contratar nuestro plan {{product.title}} por USD {{ product.price }} al mes</h4>

        <p>{{ product.description }}</p>
        <p>Paga de manera segura con tu cuenta de PayPal o Tarjeta de crdito</p>

      </div>

      <div v-if="paidFor">
        <h4>Genial, tu compra fue efectuada con xito!</h4>
        <p>En breve recibiras un correo con el detalle y credenciales</p>
      </div>
      <div class="row">
          <div class="col-md-12" ref="paypal"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
import axios from "axios";
import swal from 'sweetalert';
import emailjs from 'emailjs-com';

(function(){
    emailjs.init(process.env.VUE_APP_MY_ENV_VARIABLE);
})();

export default {
  name: "payaments",

  data: function() {
    return {
      dialog: false,
      modalShow: false,
      loaded: false,
      paidFor: false,
      orderObj: {},
      res: {}
    };
  },
  props: {
    product: Object
  },
  mounted: function() {
    const script = document.createElement("script");
    let test = process.env.VUE_APP_MY_ENV_VARIABLE_TEST;
    let live = process.env.VUE_APP_MY_ENV_VARIABLE_LIVE;
    script.src =
      `https://www.paypal.com/sdk/js?client-id=${live}`;
    script.addEventListener("load", this.setLoaded);
    document.body.appendChild(script);
  },
  methods: {
    closePaypal: function() {
        this.$emit('dialog', this.dialog); 
        this.$emit('modalShow', this.modalShow); 
    },
    setLoaded: function() {
      this.loaded = true;
      window.paypal
        .Buttons({
          createOrder: (data, actions) => {
            return actions.order.create({
              purchase_units: [
                {
                  description: this.product.description,
                  amount: {
                    currency_code: "USD",
                    value: this.product.price
                  }
                }
              ]
            });
          },
          onApprove: async (data, actions) => {
            this.orderObj = await actions.order.capture();
            this.paidFor = true;
            swal({
              title: "Buen trabajo!",
              text: "Tu compra se ha efectuado correctamente. Te llegar un email.",
              icon: "success"
            });
            this.closePaypal();
            this.saveOrder();
          },
          onError: err => {
            console.log(err);
          }
        })
        .render(this.$refs.paypal);
    },
    saveOrder: function() {
      let me = this;
      let order = this.orderObj;
      let data = {
        idOrderPay: order.id,
        deliveryDate: order.create_time,
        clientName: `${order.payer.name.given_name} ${order.payer.name.surname}`,
        clientEmail: order.payer.email_address,
        countryCode: order.payer.address.country_code,
        purchaseDesc: order.purchase_units[0].description,
        purchaseAmount: `${order.purchase_units[0].amount.currency_code} ${order.purchase_units[0].amount.value}`
      };
      axios.post(
        "/order/add",data
      )
      .then(function(response) {
        me.res = response.data.result;
        me.saveClient();
        me.sendEmail();
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    saveClient: function() {
      let me = this;
      let email = this.res.clientEmail;
      let orderId = this.res.id
      //GET CLIENTE
      axios.get(`/client/${email}`)
      .then(function(response) {
        let exists = response.data.result;
        if(!exists) {
          //CREA CLIENTE
          let sc = me;
          let name = me.orderObj.payer.name.given_name;
          let surname = me.orderObj.payer.name.surname;
          axios.post("/client/add",{
            name: name,
            surname: surname,
            email: email,
            orderid: orderId
          })
          .then(function(response) {
            console.log(response.data.result)
          })
          .catch(function(error) {
            console.log(error);
          });
        }
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    sendEmail: async function() {
      let plan = this.product.plans;
      let me = this;
      let name = me.orderObj.payer.name.given_name;
      let surname = me.orderObj.payer.name.surname;
      let email = me.res.clientEmail;
      let orden = me.res.id;
      let ordenPay = me.res.idOrderPay;
      let monto = me.orderObj.purchase_units[0].amount.currency_code + me.orderObj.purchase_units[0].amount.value
      let message = `
        Se ha efectuado la contratacion de nuestro plan de hosting ${plan} correctamente. 
        Cliente: ${name} ${surname}. Email: ${email}. Monto: ${monto}
        Nmero de rden: ${orden}. Numero de orden PayPal: ${ordenPay}

        A la brevedad recibirs un correo con las credenciales de acceso al servidor y la factura del servicio.
      `
      let data = {
        name: name,
        surname: surname,
        email: 'tedesco8@hotmail.com',
        orden: orden,
        ordenPay: ordenPay,
        plan: plan,
        monto: monto
      }
      // let data 	= {
      //   from_name: name,
      //   from_email: email,
      //   subject: 'Contratacin Servidor',
      //   message: message
      // }
      await axios.post(
        "/email/confirm",data
      )
      // emailjs.send('sendinblue', 'ventas', data)
      .then(() => {
        debugger
        console.log('Email enviado')
      }).catch( (err) => {
        debugger
        console.log(err);
      })
    }
  }
};
/* eslint-disable no-debugger */
</script>